import { NextRequest, NextResponse } from 'next/server'
import { promises as fs } from 'fs'
import path from 'path'
import crypto from 'crypto'

// Encryption key (in production, use environment variable)
const ENCRYPTION_KEY = process.env.CREDENTIALS_ENCRYPTION_KEY || 'risivo-crm-default-key-32-chars!'
const ALGORITHM = 'aes-256-gcm'

// Encrypt credential value
function encrypt(text: string): string {
  const iv = crypto.randomBytes(16)
  const cipher = crypto.createCipheriv(ALGORITHM, Buffer.from(ENCRYPTION_KEY.padEnd(32).slice(0, 32)), iv)
  let encrypted = cipher.update(text, 'utf8', 'hex')
  encrypted += cipher.final('hex')
  const authTag = cipher.getAuthTag()
  return `${iv.toString('hex')}:${authTag.toString('hex')}:${encrypted}`
}

// Decrypt credential value
function decrypt(encryptedText: string): string {
  try {
    const [ivHex, authTagHex, encrypted] = encryptedText.split(':')
    const iv = Buffer.from(ivHex, 'hex')
    const authTag = Buffer.from(authTagHex, 'hex')
    const decipher = crypto.createDecipheriv(ALGORITHM, Buffer.from(ENCRYPTION_KEY.padEnd(32).slice(0, 32)), iv)
    decipher.setAuthTag(authTag)
    let decrypted = decipher.update(encrypted, 'hex', 'utf8')
    decrypted += decipher.final('utf8')
    return decrypted
  } catch (error) {
    console.error('Decryption error:', error)
    return ''
  }
}

// Credentials storage file path
const CREDENTIALS_FILE = path.join(process.cwd(), '.credentials.json')

// Read credentials from file
async function readCredentials(): Promise<Record<string, Record<string, string>>> {
  try {
    const data = await fs.readFile(CREDENTIALS_FILE, 'utf8')
    return JSON.parse(data)
  } catch {
    return {}
  }
}

// Write credentials to file
async function writeCredentials(credentials: Record<string, Record<string, string>>): Promise<void> {
  await fs.writeFile(CREDENTIALS_FILE, JSON.stringify(credentials, null, 2))
}

// Generate .env.local content
function generateEnvContent(credentials: Record<string, Record<string, string>>): string {
  const lines: string[] = [
    '# ===========================================',
    '# RISIVO CRM - API Credentials',
    `# Auto-generated on ${new Date().toISOString()}`,
    '# DO NOT EDIT MANUALLY - Use Platform Admin',
    '# ===========================================',
    ''
  ]

  // Define category order and comments
  const categoryComments: Record<string, string> = {
    openai: '# AI Core',
    anthropic: '# AI Core (Alternative)',
    'google-gemini': '# AI Core (Alternative)',
    twilio: '# Voice & SMS',
    elevenlabs: '# Voice Synthesis',
    vapi: '# Voice AI Platform',
    synthflow: '# Voice AI Platform',
    deepgram: '# Speech-to-Text',
    heygen: '# Video Generation',
    synthesia: '# Video Generation (Alternative)',
    'd-id': '# Video Generation (Alternative)',
    stability: '# Image Generation',
    removebg: '# Image Processing',
    dataforseo: '# SEO Analytics',
    'google-search-console': '# SEO Analytics',
    semrush: '# SEO Analytics',
    cloudinary: '# Media Storage',
    'aws-s3': '# Media Storage',
    sendgrid: '# Email Service',
    stripe: '# Payments'
  }

  let lastCategory = ''

  for (const [providerId, providerCredentials] of Object.entries(credentials)) {
    const comment = categoryComments[providerId]
    if (comment && comment !== lastCategory) {
      lines.push('')
      lines.push(comment)
      lastCategory = comment
    }

    for (const [key, encryptedValue] of Object.entries(providerCredentials)) {
      if (encryptedValue) {
        try {
          const decryptedValue = decrypt(encryptedValue)
          if (decryptedValue) {
            lines.push(`${key}=${decryptedValue}`)
          }
        } catch {
          // If decryption fails, skip this credential
          console.error(`Failed to decrypt ${key}`)
        }
      }
    }
  }

  return lines.join('\n')
}

// Write .env.local file
async function writeEnvFile(credentials: Record<string, Record<string, string>>): Promise<void> {
  const envContent = generateEnvContent(credentials)
  const envPath = path.join(process.cwd(), '.env.local')
  await fs.writeFile(envPath, envContent)
}

// Get decrypted credentials for a provider (for testing)
async function getDecryptedCredentials(providerId: string): Promise<Record<string, string>> {
  const allCredentials = await readCredentials()
  const providerCreds = allCredentials[providerId] || {}
  const decrypted: Record<string, string> = {}
  
  for (const [key, encryptedValue] of Object.entries(providerCreds)) {
    if (encryptedValue) {
      decrypted[key] = decrypt(encryptedValue)
    }
  }
  
  return decrypted
}

// GET - Retrieve all credentials (masked)
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const providerId = searchParams.get('providerId')
    
    const credentials = await readCredentials()
    
    // If specific provider requested, return decrypted for testing
    if (providerId) {
      const decrypted = await getDecryptedCredentials(providerId)
      return NextResponse.json({
        success: true,
        credentials: decrypted,
        message: 'Credentials retrieved for testing'
      })
    }
    
    // Return masked credentials for all providers
    const maskedCredentials: Record<string, Record<string, string>> = {}
    const configuredProviders: string[] = []
    
    for (const [providerKey, providerCredentials] of Object.entries(credentials)) {
      maskedCredentials[providerKey] = {}
      let hasValidCredential = false
      
      for (const [key, value] of Object.entries(providerCredentials)) {
        if (value) {
          // Show only last 4 characters
          const decrypted = decrypt(value)
          if (decrypted) {
            maskedCredentials[providerKey][key] = '••••••••' + decrypted.slice(-4)
            hasValidCredential = true
          }
        }
      }
      
      if (hasValidCredential) {
        configuredProviders.push(providerKey)
      }
    }

    return NextResponse.json({
      success: true,
      credentials: maskedCredentials,
      configuredProviders,
      message: 'Credentials retrieved successfully'
    })
  } catch (error) {
    console.error('Error retrieving credentials:', error)
    return NextResponse.json({
      success: false,
      error: 'Failed to retrieve credentials'
    }, { status: 500 })
  }
}

// POST - Save credentials
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { providerId, credentials: newCredentials } = body

    if (!providerId || !newCredentials) {
      return NextResponse.json({
        success: false,
        error: 'Missing providerId or credentials'
      }, { status: 400 })
    }

    // Read existing credentials
    const allCredentials = await readCredentials()

    // Encrypt and store new credentials
    allCredentials[providerId] = {}
    for (const [key, value] of Object.entries(newCredentials as Record<string, string>)) {
      if (value && value.trim()) {
        allCredentials[providerId][key] = encrypt(value.trim())
      }
    }

    // Write to credentials file
    await writeCredentials(allCredentials)

    // Update .env.local
    await writeEnvFile(allCredentials)

    return NextResponse.json({
      success: true,
      message: `Credentials for ${providerId} saved successfully`,
      envUpdated: true
    })
  } catch (error) {
    console.error('Error saving credentials:', error)
    return NextResponse.json({
      success: false,
      error: 'Failed to save credentials'
    }, { status: 500 })
  }
}

// DELETE - Remove credentials for a provider
export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const providerId = searchParams.get('providerId')

    if (!providerId) {
      return NextResponse.json({
        success: false,
        error: 'Missing providerId'
      }, { status: 400 })
    }

    // Read existing credentials
    const allCredentials = await readCredentials()

    // Remove provider credentials
    delete allCredentials[providerId]

    // Write to credentials file
    await writeCredentials(allCredentials)

    // Update .env.local
    await writeEnvFile(allCredentials)

    return NextResponse.json({
      success: true,
      message: `Credentials for ${providerId} removed successfully`,
      envUpdated: true
    })
  } catch (error) {
    return NextResponse.json({
      success: false,
      error: 'Failed to remove credentials'
    }, { status: 500 })
  }
}

// PUT - Test credentials for a provider
export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()
    const { providerId, credentials: testCredentials } = body

    if (!providerId) {
      return NextResponse.json({
        success: false,
        error: 'Missing providerId'
      }, { status: 400 })
    }

    // If no credentials provided, fetch from storage
    let credentialsToTest = testCredentials
    if (!testCredentials || Object.keys(testCredentials).length === 0) {
      credentialsToTest = await getDecryptedCredentials(providerId)
    }

    // Provider-specific connection tests
    let testResult: { success: boolean; message: string; details?: string } = { success: false, message: 'Unknown provider' }

    switch (providerId) {
      case 'openai':
        testResult = await testOpenAI(credentialsToTest)
        break
      case 'twilio':
        testResult = await testTwilio(credentialsToTest)
        break
      case 'elevenlabs':
        testResult = await testElevenLabs(credentialsToTest)
        break
      case 'heygen':
        testResult = await testHeyGen(credentialsToTest)
        break
      case 'stripe':
        testResult = await testStripe(credentialsToTest)
        break
      case 'sendgrid':
        testResult = await testSendGrid(credentialsToTest)
        break
      case 'anthropic':
        testResult = await testAnthropic(credentialsToTest)
        break
      case 'google-gemini':
        testResult = await testGoogleGemini(credentialsToTest)
        break
      case 'cloudinary':
        testResult = await testCloudinary(credentialsToTest)
        break
      case 'dataforseo':
        testResult = await testDataForSEO(credentialsToTest)
        break
      default:
        testResult = { 
          success: true, 
          message: 'Connection test not available for this provider. Credentials saved.',
          details: 'Manual verification recommended'
        }
    }

    return NextResponse.json(testResult)
  } catch (error) {
    console.error('Connection test error:', error)
    return NextResponse.json({
      success: false,
      error: 'Connection test failed'
    }, { status: 500 })
  }
}

// =====================
// Provider Test Functions
// =====================

async function testOpenAI(credentials: Record<string, string>): Promise<{ success: boolean; message: string; details?: string }> {
  try {
    if (!credentials.OPENAI_API_KEY) {
      return { success: false, message: 'OpenAI API key is required' }
    }
    
    const response = await fetch('https://api.openai.com/v1/models', {
      headers: {
        'Authorization': `Bearer ${credentials.OPENAI_API_KEY}`
      }
    })
    
    if (response.ok) {
      const data = await response.json()
      const modelCount = data.data?.length || 0
      return { 
        success: true, 
        message: 'OpenAI API connection successful',
        details: `Access to ${modelCount} models`
      }
    }
    
    const error = await response.json()
    return { 
      success: false, 
      message: 'Invalid OpenAI API key',
      details: error.error?.message || 'Authentication failed'
    }
  } catch (error) {
    return { success: false, message: 'Failed to connect to OpenAI', details: String(error) }
  }
}

async function testTwilio(credentials: Record<string, string>): Promise<{ success: boolean; message: string; details?: string }> {
  try {
    if (!credentials.TWILIO_ACCOUNT_SID || !credentials.TWILIO_AUTH_TOKEN) {
      return { success: false, message: 'Twilio Account SID and Auth Token are required' }
    }
    
    const auth = Buffer.from(`${credentials.TWILIO_ACCOUNT_SID}:${credentials.TWILIO_AUTH_TOKEN}`).toString('base64')
    const response = await fetch(`https://api.twilio.com/2010-04-01/Accounts/${credentials.TWILIO_ACCOUNT_SID}.json`, {
      headers: {
        'Authorization': `Basic ${auth}`
      }
    })
    
    if (response.ok) {
      const data = await response.json()
      return { 
        success: true, 
        message: 'Twilio connection successful',
        details: `Account: ${data.friendly_name}, Status: ${data.status}`
      }
    }
    
    return { success: false, message: 'Invalid Twilio credentials' }
  } catch (error) {
    return { success: false, message: 'Failed to connect to Twilio', details: String(error) }
  }
}

